import React from 'react';
import {useState,useEffect} from 'react';
import {useHistory,useParams} from 'react-router-dom';
import axios from 'axios';
 const Edituser=()=>{
let history=useHistory();
const {id}=useParams();
const [user,setUser]=useState({
	name:"",
	username:"",
	email:"",
	phone:"",
	website:""
});
const {name,username,email,phone,website}=user;
const onInputChange=e=>{
setUser({...user,[e.target.name]:e.target.value});
};
useEffect(()=>
  {
    loadUsers();

  },[]);
const onSubmit=async e=>{
e.preventDefault();
await axios.put( `http://localhost:3001/users/${id}`,user);
history.push("/");
};

const loadUsers=async e=>{
const result=await axios.get( `http://localhost:3001/users/${id}`);
setUser(result.data);
};

return (
<div className="container">
<div className="py-4">

<h1>Edit User</h1>
<form onSubmit={e=>onSubmit(e)}>
  <div class="form-group">
    <label for="Name">Enter Name</label>
    <input type="text" class="form-control" id="name" name="name" value={name} aria-describedby="emailHelp" placeholder="Enter name" onChange={e=>onInputChange(e)}/>
  {  /*<small id="emailHelp" class="form-text text-muted">We'll never share your email with anyone else.</small>*/}
  </div>
  <div class="form-group">
    <label for="User Name">Enter Username</label>
    <input type="text" class="form-control" id="username" name="username" value={username} placeholder="Enter user name " onChange={e=>onInputChange(e)} />
  </div>
  <div class="form-group">
    <label for="Email">Enter email</label>
    <input type="text" class="form-control" id="email" name="email" value={email} placeholder="Enter your email" onChange={e=>onInputChange(e)} />
  </div>
   <div class="form-group">
    <label for="Phone">Phone</label>
    <input type="text" class="form-control" id="phone" name="phone" value={phone} placeholder="Enter your phone" onChange={e=>onInputChange(e)}/>
  </div>
   <div class="form-group">
    <label for="Website">Website</label>
    <input type="text" class="form-control" id="website" name="website" value={website} placeholder="Enter your website" onChange={e=>onInputChange(e)} />
  </div>
  
  <button  className="btn  btn-block btn-warning" >Update User</button>
</form>
</div>
</div>
	);

}
export default Edituser;